.GameRoomInfo {
  padding: 10px;
  background-color: white;
  position: fixed;
  top: 1em;
  right: 1em;
  border: 2px solid black;
  z-index: -1;
  border-radius: 5px;
  word-wrap: break-word;
}

.GameForm, .Instructions {
  background-color: #f9d522;
  padding: 12px;
  border: 3px solid black;
  border-radius: 20px;
}

.Instructions span {
  font-size:20px;
}


