body, html {
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image:-webkit-radial-gradient(#8fcbf5,#73c2fb, #6ab8fc,#2390fcf5);
  background-image:-o-radial-gradient(#8fcbf5,#73c2fb, #6ab8fc,#2390fcf5);
  background-image:radial-gradient(#8fcbf5,#73c2fb, #6ab8fc,#2390fcf5); 
  /* background-image: url("https://i.imgur.com/OiHrDus.jpg"); */
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  overflow: scroll;
  word-wrap: break-word;
}

